.dashboard-tabs {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.tab-button {
    padding: 10px 20px;
    cursor: pointer;
    border: none;
    background-color: #f0f0f0;
    margin-right: 5px;
    outline: none;
}

.tab-button.active {
    background-color: #007bff;
    color: white;
}
